import {
	uniNavBar
} from '@dcloudio/uni-ui'
export default {
	components: {
		uniNavBar
	},
	onShow: function() {

		try {
			const value = uni.getStorageSync('editData');
			if (value) {

				this.listdata = value;
				try {
					uni.removeStorageSync('editData');
				} catch (e) {
					// error
				}
			}
		} catch (e) {
			// error
		}
	},
	onLoad: function(option) {

		if (option.item) {
			this.listdata = JSON.parse(option.item);
		}


	},
	props: {},
	data() {
		return {
			listdata: {}
		}
	},
	methods: {
		toback() {
			uni.navigateBack({
				delta: 1,
				animationType: 'pop-out',
				animationDuration: 200
			});
		},
		deldata() {
			const _this = this;
			uni.showModal({
				title: '提示',
				content: '是否确认删除此条数据？',
				success: function(res) {
					if (res.confirm) {
						uniCloud.callFunction({
							name: 'remove',
							data: {
								dataId: _this.listdata._id
							}
						}).then((res) => {
							uni.showToast({
								title: `${res.result.msg}`,
								duration: 2000
							});
							setTimeout(() => {
								_this.toback()
							}, 2000)

						}).catch((err) => {
							uni.showModal({
								content: `操作失败，请重新操作`,
								showCancel: false
							})
							console.error(err)
						})

					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});
		},
		// 修改数据
		editdata() {
			let _item = JSON.stringify(this.listdata)
			uni.navigateTo({
				url: './edit?item=' + _item
			});
		}
	}
}
